import React, { Fragment, useState } from 'react';

import { Layout, Menu } from 'antd';

import { MenuRoute } from '../../../constants/MenuRoute';
import { Link } from 'react-router-dom';

import logo from '../../../assets/or-white.png'

const { Sider } = Layout;
const { SubMenu } = Menu;

const AppSidenav = () => {

    const [collapsed, setCollapsed] = useState(true);

    const onCollapse = () => {
        setCollapsed(!collapsed)
    };
    return (
        <Sider collapsible collapsed={collapsed} onCollapse={onCollapse}>
            <div className='logo'>
                <img src={logo} alt="OR" className='logo-icon' />
            </div>
            <Menu theme="dark" defaultSelectedKeys={['dashboard']} mode="inline">

                {MenuRoute.map(menuitem => (
                    <Fragment key={menuitem.key}>
                        {
                            menuitem.routes ?
                                <SubMenu key={menuitem.key} icon={menuitem.iconName} title={menuitem.menuName}>
                                    {menuitem.routes.map(subItem => (
                                        <Menu.Item key={subItem.key} icon={subItem.iconName}>
                                            <Link to={subItem.path}>
                                                {subItem.menuName}
                                            </Link>
                                        </Menu.Item>
                                    ))}
                                </SubMenu>
                                : <Menu.Item key={menuitem.key} icon={menuitem.iconName}>
                                    <Link to={menuitem.path} key={menuitem.key}>
                                        {menuitem.menuName}
                                    </Link>
                                </Menu.Item>
                        }
                    </Fragment>
                ))}
            </Menu>
        </Sider>
    );
};

export default AppSidenav;